import { Switch, Route } from "react-router-dom";
import Home from "./Home";
import BookDetails from "./books/BookDetails";
import Account from "./account/Account";
import SignInForm from "./forms/SignInForm";
import SignUpForm from "./forms/SignUpForm";
import { Posts } from "./forumComponents/Posts";
import Cart from "./cart/Cart";
import About from "./about/AboutUs";
import Recommend from "./recommend/recommendAll";

const Content = () => (
  <Switch>
    <Route exact path="/jfdzr4-the-best-front/">
      <Home />
    </Route>
    <Route path="/jfdzr4-the-best-front/book/:id">
      <BookDetails />
    </Route>
    <Route path="/jfdzr4-the-best-front/account">
      <Account />
    </Route>
    <Route path="/jfdzr4-the-best-front/about">
      <About />
    </Route>
    <Route path="/jfdzr4-the-best-front/cart">
      <Cart />
    </Route>
    <Route path="/jfdzr4-the-best-front/sign-in">
      <SignInForm />
    </Route>
    <Route path="/jfdzr4-the-best-front/sign-up">
      <SignUpForm />
    </Route>
    <Route path="/jfdzr4-the-best-front/forum">
      <Posts />
    </Route>
    <Route path="/jfdzr4-the-best-front/recommend">
      <Recommend />
    </Route>
    
  </Switch>
);

export default Content;
